<template>
 <div id="show-blogs" v-theme:column=" 'narrow' ">
     <h1>博客总览</h1>
     <input type="search" placeholder="搜索" v-model="search">
     <div class="single-blog" v-for="blog in filteredBlogs">
        <router-link v-bind:to=" '/blog/' + blog.id">
            <h2 v-rainbow>{{blog.title | to-uppercase}}</h2>
        </router-link>
         <article>
             {{blog.content | snippet}}
         </article>

     </div>
 </div>
</template>
<script>
    export default {
        name: 'show-blogs',
        data(){
            return{
                blogs:[],
                search:""
            }
        },
        created() {
            this.$http.get('https://vuedemo-b1233-14e43.firebaseio.com/posts.json')
                .then(function (data) {
                    return data.json();
                    // this.blogs = data.body.slice(0,10);
                    // console.log(this.blogs)
                })
                .then(function (data) {
                    var blogsArray = [];
                    for (let key in data){
                        data[key].id =key;
                        blogsArray.push(data[key]);
                    }
                    this.blogs = blogsArray;
                })
        },
        computed:{
            filteredBlogs:function () {
                return this.blogs.filter((blog)=>{
                    return blog.title.match(this.search)
                })
            }
        },

    }
</script>

<style scoped>
    #show-blogs{
        max-width: 800px;
        margin: 0 auto;
    }
    .single-blog{
        padding: 20px;
        margin: 20px 0;
        box-sizing: border-box;
        background: #eee;
        border: 1px dotted #aaa;
    }
    #show-blogs a{
        color: #444;
        text-decoration: none;
    }
    h1{
        text-align: center;
    }
    input[type="search"]{
       padding: 8px;
        width: 100%;
        box-sizing: border-box;
       text-align: center;
        border-radius: 4px;

    }

</style>
